import React from 'react';
import HeaderPublic from '../shared/components/HeaderPublic';

interface ILayouPublicProps {
	children?: React.ReactNode;
}

const LayouPublic = ({ children }: ILayouPublicProps) => {
	return (
		<>
			<HeaderPublic />
			<div className="container-fluid pt-3">{children}</div>
		</>
	);
};

export default LayouPublic;
